import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import styled from 'styled-components';
import { HashLink as Link } from 'react-router-hash-link';

const Styles = styled.div`
  .projectLinks{
    font-style:italic;
    background:rgb(33, 150, 243);
    box-shadow: 0 0 0 4px #1976d2, inset 0 0px 0 rgba(0, 0, 0, 0.08), 0 0px 0 0px rgba(0, 0, 0, 0.05);

    padding:10px;
    height:50px;

    transition: transform .2s; /* Animation */
    &:hover {
      color: white;
      font-size: 150%;
      font-weight:bold;
      font-style:normal;
      padding-top:5px;
      padding-bottom:5px;
    }
  }

  a {
    color:black;
    height:120%;
    width:100%;
    display:flex;
    justify-content: center;

    &:hover {
      color: white;
      text-decoration: none;
    }
  }
`;

const FeaturedProjects = () => (
  <Styles>
    <Container>
      <Row>
        <Col className ="projectLinks">
          <Link to="/projects#7">MinecraftBot</Link>
        </Col>
        <Col className ="projectLinks">
          <Link to="/projects#6">CarePost</Link>
        </Col>
        <Col className ="projectLinks">
          <Link to="/projects#5">Offroad Vehicle</Link>
        </Col>
      </Row>
      <Row>
        <Col className ="projectLinks">
          <Link to="/projects#3">F1 in School</Link>
        </Col>
        <Col className ="projectLinks">
          <Link to="/projects#1">Kirby Saves Christmas</Link>
        </Col>
      </Row>
    </Container>
  </Styles>
)

export default FeaturedProjects;
